.container {
    width: 22rem;
    height: 32rem;
    margin-right: 60px;
    border: solid #1698d9;
    border-radius: 10px;
    background-color: #fff;
    cursor: pointer;
    // clip-path: polygon(40px 0, 100% 0, 100% 100%, 0 100%, 0 40px);

    @media(max-width: 1024px) {
        &:nth-child(3n) {
            margin: 60px 0 0 180px;
        }
    }

    @media(max-width: 768px) {
        margin-left: -8rem;

        &:nth-child(3n) {
            margin-right: 100px;
        }
    }
}

.container2 {
    width: 22rem;
    height: 32rem;
    margin-right: 60px;
    border: solid red;
    border-radius: 10px;
    background-color: #fff;
    cursor: pointer;
    // clip-path: polygon(40px 0, 100% 0, 100% 100%, 0 100%, 0 40px);

    @media(max-width: 1024px) {
        &:nth-child(3n) {
            margin: 60px 0 0 180px;
        }
    }
}

.containerfalse {
    opacity: 0.5;
    width: 22rem;
    height: 32rem;
    margin-right: 60px;
    border: solid grey;
    border-radius: 10px;
    background-color: #fff;
    cursor: pointer;
    // clip-path: polygon(40px 0, 100% 0, 100% 100%, 0 100%, 0 40px);

    @media(max-width: 1024px) {
        &:nth-child(3n) {
            margin: 60px 0 0 180px;
        }
    }

    @media(max-width: 768px) {

        &:nth-child(3n) {
            margin-left: 6rem;
        }
    }
}

.wrapper {
    cursor: not-allowed;
}




.itemcontainer {
    width: 60%;
    margin: 0 auto;
    display: flex;

    @media(max-width: 1024px) {
        width: 52rem;
        flex-wrap: wrap;

        &:nth-child(2n) {
            margin-left: 160px;
        }
    }
}

.weight {
    width: 80px;
    height: 80px;
    background-color: #1698d9;
    position: absolute;
    margin-top: -6rem;
    margin-left: 15rem;
    border-radius: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    flex-direction: column;
    color: red;
}


.weightfalse {
    width: 80px;
    height: 80px;
    background-color: grey;
    position: absolute;
    margin-top: -6rem;
    margin-left: 15rem;
    border-radius: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    flex-direction: column;
    color: red;
}

